      // Ð¡Ð¾Ð·Ð´Ð°ÑÐ¼ Ð¸ ÑÐµÐ½Ð´ÐµÑÐ¸Ð¼ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ ÑÐ¾ÑÐºÐ¸ Ð¼Ð°ÑÑÑÑÑÐ°
      const pointView = new PointView(point, destination, pointOffers);
      render(pointView, this.pointsListViewComponent.getElement());
